import React from 'react'
import PropTypes from 'prop-types';
import But from './styles'

const Counter = (props) => {
    return (
      <div className='wrapper'>
        <h3>{props.count}</h3>
        <But operation = {'Plus'}
            event = {props.inc}></But> 
        <But operation = {'Minus'}
            event = {props.dec}></But>
        <But operation = {'Reset'}
            event = {props.reset}></But>
     </div>
    )
}

Counter.propTypes = {
    count: PropTypes.number,
    inc: PropTypes.func,
    dec: PropTypes.func,
    reset: PropTypes.func
}

export default Counter




